body {
  font-family: 'Roboto', sans-serif;
  max-width: 1440px;
  margin: auto;
  padding: 40px;
}

.breed-select {
  width: 60%;
  margin: auto;
}

.is-hidden {
  display: none;
}

.cat-title {
  text-align: center;
}

.description-container {
  display: flex;
  gap: 24px;
}

.cat-image {
  max-width: calc((100% - 24px) / 2);
  height: auto;
}

.cat-description {
  font-weight: 400;
  font-size: 1.6rem;
  max-width: calc((100% - 24px) / 2);
}



/* Loader */

.loading {
  /* margin: auto; */
  width: 48px;
  height: 48px;
  border: 3px solid #FFF;
  border-radius: 50%;
  display: inline-block;
  position: absolute;
  left: 45%;
  top: 25%;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
} 
.loading::after {
  content: '';  
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-bottom-color: #FF3D00;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
} 