body {
  max-width: 1440px;
  margin: auto;
  padding: 40px;
  font-family: Roboto, sans-serif;
}

.breed-select {
  width: 60%;
  margin: auto;
}

.is-hidden {
  display: none;
}

.cat-title {
  text-align: center;
}

.description-container {
  gap: 24px;
  display: flex;
}

.cat-image {
  max-width: calc(50% - 12px);
  height: auto;
}

.cat-description {
  max-width: calc(50% - 12px);
  font-size: 1.6rem;
  font-weight: 400;
}

.loading {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 25%;
  left: 45%;
}

.loading:after {
  content: "";
  box-sizing: border-box;
  width: 56px;
  height: 56px;
  border: 3px solid rgba(0, 0, 0, 0);
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

